import React from "react";
import { Card, Image, Divider } from 'semantic-ui-react';

const CardArticle = (props) => (
  <Card
    href={props.article.link}
    target = "_blank"
    color='purple'
    style={{backgroundColor: '#000'}}>
    <Image src={props.article.image} wrapped ui={false} />
    <Card.Content>
      <Card.Header style={{color: '#fff'}}>{props.article.title}</Card.Header>
      <Divider clearing section/>
      <Card.Description>
        {
          props.article.content.split('\\n').map((item, i) => {
            return <p key={i} style={{color: '#fff'}}>{item}</p>;
          })
        }
      </Card.Description>
    </Card.Content>
  </Card>
);

export default CardArticle;